.gallery .inner {
  position: relative;
}
.gallery .inner .loading {
  position: absolute;
  top: 25%;
  left: 45%;
  width: 200px;
  height: 200px;
}
.gallery .inner .list {
  opacity: 0;
  transform: translateY(200px);
  transition: 1s;
  position: relative;
  margin-left: 220px;
  border-top: 1px solid #555;
}
.gallery .inner .list.on {
  opacity: 1;
  transform: translateY(0px);
}
