  width: 100%;
  padding: 12px;
  border: 1px solid #555;
}
.gallery .inner .list ul .item .inner img {
  width: 100%;
  margin-bottom: 10px;
  object-fit: cover;
  opacity: 0.5;
  transition: 0.5s;
}
.gallery .inner .list ul .item .inner h2 {
  font-size: 15px;
  padding-left: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #555;
  font-weight: normal;
  color: #000;
  height: 30px;
  margin-bottom: 20px;
}
.gallery .inner .list ul .item .inner p {
  font-size: 13px;
  text-align: right;
  color: #555;
}
.gallery .inner .list ul .item .inner a {
  position: absolute;
  top: 50%;
  display: none;
  color: #fff;
  left: 50%;
  transform: translate(-50%, -50%);
}
.gallery .inner .list ul .item .inner:hover img {
  opacity: 1;
}
.gallery .inner .list ul .item .inner:hover a {
  display: block;
}

