.youtube .inner .frame::after {
  content: "";
  display: block;
  clear: both;
}
.youtube .inner .frame article {
  width: 100%;
  float: left;
  padding: 15px;
  overflow: hidden;
}
.youtube .inner .frame article .inner {
  width: 100%;
  height: 100%;
  padding: 25px 0px;
  border-top: 2px solid #bebebe;
  /*
      word-break: 텍스트의 줄바꿈 속성을 지정
      - normal (한글은 한글자 기준, 영어는 단어기준으로 줄바꿈됨) default
      - break-all (한글과 영문 모두 글자기준으로 줄바꿈)
      - keep-all (한글 영어 모두 단어기준으로 줄바꿈)
    */
}
.youtube .inner .frame article .inner .txt {
  float: left;
  width: 60%;
}
.youtube .inner .frame article .inner .txt h2 {
  font-size: 12px;
  color: #adadad;
  word-break: break-all;
  float: left;
  width: 50%;
}
.youtube .inner .frame article .inner .txt p {
  width: 50%;
  font-size: 13px;
  float: left;
  color: #5e5e5e;
  word-break: keep-all;
}
.youtube .inner .frame article .inner .pic {
  position: relative;
  float: left;
  width: 40%;
  overflow: hidden;
  cursor: pointer;
}
.youtube .inner .frame article .inner .pic img {
  width: 100%;
  height: 100%;
  opacity: 0.5;
  object-fit: cover;
  transition: 1s;
}
.youtube .inner .frame article .inner .pic .btn_click {
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  text-decoration-line: underline;
}
.youtube .inner .frame article .inner .pic:hover .btn_click {
  display: block;
}
.youtube .inner .frame article .inner .pic:hover img {
  opacity: 1;
}
.youtube .inner .frame article .inner::after {
  content: "";
  clear: both;
  display: block;
}
.youtube .inner .frame article .btn_show {
  font-size: 11px;
  font-weight: bold;
  margin-left: 30%;
  color: #666;
  display: block;
  text-decoration-line: underline;
  margin-top: 20px;
}
.youtube .inner .frame article:last-child {
  border-bottom: 2px solid #bebebe;
}
.youtube .inner .pop {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.9);
  padding: 5vw;
}
.youtube .inner .pop iframe {
  border: none;
  width: 100%;
  height: 100%;
}
.youtube .inner .pop span {
  position: absolute;
  top: 3vw;
  right: 5vw;
  font: bold 14px/1 "arial";
  color: #fff;
  cursor: pointer;
}
